import { lazy } from 'react';

// use lazy for better code splitting, a.k.a. load faster
const Dashboard = lazy(() => import('../pages/admin/Dashboard'));
const Admins = lazy(() => import('../pages/admin/Admins'));
const Chains = lazy(() => import('../pages/admin/Chains'));
const ChainsAccount = lazy(() => import('../pages/admin/ChainsAccount'));
const SingleAssets = lazy(() => import('../pages/admin/SingleAssets'));
const DualAssets = lazy(() => import('../pages/admin/DualAssets'));
const PoolManagementPage = lazy(() => import('../pages/admin/PoolManagement'));
const Users = lazy(() => import('../pages/admin/Users'));
const Login = lazy(() => import('../pages/auth/Login'));
const ForgotPassword = lazy(() => import('../pages/auth/ForgotPassword'));
const ResetPassword = lazy(() => import('../pages/auth/ResetPassword'));
const Confirmation = lazy(() => import('../pages/auth/Confirmation'));
const Page404 = lazy(() => import('../pages/404'));
const ComingSoonPage = lazy(() => import('../pages/admin/ComingSoon'));

const commonRoutes = [
  {
    path: '/dashboard',
    component: Dashboard
  }
];

const superAdminRoutes = [
  {
    path: '/admins',
    component: Admins
  }
];

const adminRoutes = [
  {
    path: '/transactions', // the url
    component: ComingSoonPage // view rendered
  },
  {
    path: '/deposit', // the url
    component: ComingSoonPage // view rendered
  },
  {
    path: '/withdraw', // the url
    component: ComingSoonPage // view rendered
  },
  {
    path: '/chains',
    component: Chains
  },
  {
    path: '/chains/chainsAccount/:id',
    component: ChainsAccount
  },
  // {
  //   path: '/singleAssets',
  //   component: SingleAssets
  // },
  // ,
  // {
  //   path: '/dualassets',
  //   component: DualAssets
  // },
  {
    path: '/poolManagement',
    component: PoolManagementPage
  },
  {
    path: '/users',
    component: Users
  },
  {
    path: '/abc',
    component: Page404
  }
];

const userRoutes = [];

const managerRoutes = [];

const routes = commonRoutes;

const GetRoutes = role => {
  return role === 'super'
    ? routes.concat(superAdminRoutes)
    : role === 'admin'
    ? routes.concat(adminRoutes)
    : role === 'user'
    ? routes.concat(userRoutes)
    : role === 'manager'
    ? routes.concat(managerRoutes)
    : routes;
};

const authRoutes = [
  {
    path: '/login',
    component: Login
  },
  {
    path: '/forgot-password',
    component: ForgotPassword
  },
  {
    path: '/reset-password',
    component: ResetPassword
  },
  {
    path: '/confirmation',
    component: Confirmation
  }
];

export { routes, authRoutes, GetRoutes };
