import { useHistory } from 'react-router-dom';

const commonRoutes = [
  {
    path: '/dashboard',
    icon: 'HomeIcon',
    name: 'Dashboard'
  }
];

const adminRoutes = [
  {
    path: '/users',
    icon: 'UserManagement',
    name: 'User Management'
  },
  { path: '/chains', icon: 'ChainIcon', name: 'Chain Management' },

  // {
  //   path: '/singleAssets',
  //   icon: 'SingleAsset',
  //   name: 'Single Assets'
  // },
  // {
  //   path: '/dualassets',
  //   icon: 'DoubleAssets',
  //   name: 'Dual Assets'
  // },
  {
    path: '/poolManagement',
    icon: 'DoubleAssets',
    name: 'Pool Management'
  },
  {
    path: '/transactions',
    icon: 'Transactions',
    name: 'Transactions'
  },
  {
    path: '/deposit',
    icon: 'Deposit',
    name: 'Deposit'
  },
  {
    path: '/withdraw',
    icon: 'Withdraw',
    name: 'Withdraw'
  }
];

const superAdminRoutes = [
  {
    path: '/admins',
    icon: 'UserManagement',
    name: 'Broker Management'
  }
];

const userRoutes = [
  {
    path: '/admins',
    icon: 'UserManagement',
    name: 'User Management'
  }
];

const managerRoutes = [
  {
    path: '/admins',
    icon: 'UserManagement',
    name: 'User Management'
  }
];

const routes = commonRoutes;

const GetRoutes = role => {
  return role === 'super'
    ? routes.concat(superAdminRoutes)
    : role === 'admin'
    ? routes.concat(adminRoutes)
    : role === 'user'
    ? routes.concat(userRoutes)
    : role === 'manager'
    ? routes.concat(managerRoutes)
    : routes;
};

const GetActiveTabHeading = role => {
  const history = useHistory();
  const activeTab = GetRoutes(role).filter(item =>
    item.path.includes(history.location.pathname)
  );
  return activeTab && activeTab[0]
    ? [activeTab[0].name, activeTab[0].subName]
    : [];
};

export {
  GetActiveTabHeading,
  GetRoutes,
  routes,
  commonRoutes,
  superAdminRoutes,
  adminRoutes,
  userRoutes,
  managerRoutes
};
