import React from 'react';

function Main(props) {
  return (
    <main
      className={`flex flex-1 flex-col h-full overflow-y-auto dark:bg-slate-700 app-cont ${
        props.menuType === 'bottom'
          ? 'lg:bottom-app-content'
          : props.menuType === 'left' ?
           (props.toggleSideBar
          ? 'lg:collapse-app-cont'
          : 'lg:expand-app-cont') : ''
      }`}>
      <div className="p-0 w-full h-full">
        <div className="grid mx-auto h-full">{props.children}</div>
      </div>
    </main>
  );
}

export default Main;
