import React, {
  useState,
  useContext,
  Suspense,
  useEffect,
  lazy,
  useCallback
} from 'react';
import { Switch, Route, Redirect, useLocation } from 'react-router-dom';
import Sidebar from '../components/shared/Sidebar';
import Header from '../components/shared/Header';
import Main from '../containers/Main';
import ThemedSuspense from '../components/shared/ThemedSuspense';
import { SidebarContext } from '../context/SidebarContext';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import useLocalStorage from '../hooks/useLocalStorage';
import { routes, GetRoutes } from '../routes/index';

const Page404 = lazy(() => import('../pages/404'));

function Layout() {
  const menuType = 'left';

  const { isSidebarOpen, closeSidebar } = useContext(SidebarContext);
  const [toggleSideBar, setToggleSideBar] = useState(false);

  const location = useLocation();
  const user = useLocalStorage('user')[0];

  const makeRoutes = GetRoutes(user && user.role);

  useEffect(() => {
    closeSidebar('layout');
  }, [location]);

  const HandleToggleSideBar = useCallback(() => {
    setToggleSideBar(!toggleSideBar);
  }, [toggleSideBar]);
  return (
    <div
      className={`flex h-screen bg-gray-50 dark:bg-gray-900 ${isSidebarOpen &&
        'overflow-hidden'}`}>
      <Sidebar
        menuType={menuType}
        toggleSideBar={toggleSideBar}
        HandleToggleSideBar={HandleToggleSideBar}
      />
      <div className="flex flex-col flex-1 w-full">
        <Header menuType={menuType} toggleSideBar={toggleSideBar} />
        <ToastContainer
          position="bottom-right"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          theme="colored"
          pauseOnHover
        />
        <Main menuType={menuType} toggleSideBar={toggleSideBar}>
          <Suspense fallback={<ThemedSuspense />}>
            <Switch>
              {makeRoutes.map((route, i) => {
                return route.component ? (
                  <Route
                    key={i}
                    exact={true}
                    path={`${route.path}`}
                    render={props => <route.component {...props} />}
                  />
                ) : null;
              })}
              <Redirect exact from="/" to="/dashboard" />
              <Redirect to="/dashboard" />
              <Route component={Page404} />
            </Switch>
          </Suspense>
        </Main>
      </div>
    </div>
  );
}

export default Layout;
